/* Variables */
/* Widgets */
.wpacw__cats a {
  position: relative;
  display: block;
  margin-bottom: 20px;
  height: 102px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #f8f8f8;
  transition: all 0.3s cubic-bezier(0.52, 0.16, 0.24, 1);
}

.wpacw__cats a > div {
  position: absolute;
  display: block;
  top: 21px;
  left: -10px;
  font-size: 0;
}

.wpacw__cats a > div span {
  color: #fff;
  position: relative;
  display: inline-block;
  font-size: 10px;
  font-weight: 500;
  line-height: 12px;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-decoration: none;
  margin: 0;
}

.wpacw__cats a > div span:first-child {
  background-color: #000;
  padding: 9px 14px;
}

.wpacw__cats a > div span:last-child {
  background-color: rgba(0, 0, 0, 0.65);
  font-size: 10px;
  padding: 9px 10px;
}

.wpacw__cats a:hover {
  opacity: .85;
}

.wpacw__cats.style2 > a, .wpacw__cats.style3 > a {
  display: flex;
  align-items: center;
  justify-content: center;
}

.wpacw__cats.style2 > a > div, .wpacw__cats.style3 > a > div {
  position: relative;
  top: auto;
  left: auto;
}

.wpacw__cats.style3 > a {
  height: 200px;
}

.wpacw__cats.style3 > a > div span {
  color: #000;
}

.wpacw__cats.style3 > a > div span:first-child {
  background-color: #fff;
  padding: 13px 18px;
}

.wpacw__cats.style3 > a > div span:last-child {
  background-color: #000;
  font-size: 10px;
  position: absolute;
  text-align: center;
  border-radius: 100px;
  line-height: 22px;
  width: 22px;
  height: 22px;
  color: #fff;
  margin-left: -7px;
  margin-top: -5px;
  padding: 0;
}

.wpacw__cats.style4 .wpacw__cat {
  display: flex;
  align-items: center;
}

.wpacw__cats.style4 .wpacw__cat > a:first-child {
  border-radius: 100px;
  width: 100px;
  height: 100px;
  margin-right: 20px;
}

.wpacw__cats.style4 .wpacw__cat > a:first-child span {
  background-color: #000;
  font-size: 10px;
  position: absolute;
  top: 0;
  right: 10px;
  text-align: center;
  border-radius: 100px;
  line-height: 22px;
  width: 22px;
  height: 22px;
  color: #fff;
  padding: 0;
}

.wpacw__cats.style4 .wpacw__cat a:last-child {
  display: flex;
  align-items: center;
  background-color: transparent;
}

.wpacw__cats.style4 > a:first-child {
  border-radius: 100px;
  width: 100px;
  height: 100px;
  margin-right: 15px;
}

.wpacw__cats.style4 > a:first-child > div span:first-child {
  display: none;
}

.wpacw__cats.style4 > a:first-child > div span:last-child {
  background-color: #000;
  font-size: 10px;
  position: absolute;
  top: 0;
  right: 0;
  text-align: center;
  border-radius: 100px;
  line-height: 22px;
  width: 22px;
  height: 22px;
  color: #fff;
  margin-left: -7px;
  margin-top: -5px;
  padding: 0;
}
